.card {
  box-shadow: 3px 7px 22px -7px rgb(0 0 0 / 45%);
  cursor: pointer;
  border-radius: 5px;
  transition: 0.5s;
}

.card:hover {
  box-shadow: 18px 30px 27px 4px rgb(0 0 0 / 40%);
  transform: scale(1.02);
  transition: 0.25s;
}

.img {
  border-radius: 5px 5px 0 0;
  background-color: darkgrey;
}

.team__about {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 0 0 5px 5px;
  padding: 20px 20px 22px 20px;
  background: var(--form-color);
}

.member__title {
  margin-bottom: 6px;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  text-transform: uppercase;
  color: var(--main-text);
}

.role__text {
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  color: var(--secondary-text);
}

.social__list {
  display: flex;
  justify-content: center;
}

.list__item {
  margin: 10px;
  border-radius: 50%;
  transition: 0.25s;
}

.list__item:hover {
  box-shadow: 8px 4px 8px 0px rgb(0 0 0 / 45%);
  transform: scale(1.2);
  transition: 0.25s;
}
